/* ==============
  Card
===================*/
.card {
  /*border: 1px solid rgba($muted,0.12);*/
  border: none;
  border-radius:0px;
  box-shadow: 0 2px 5px 0 rgba(74, 144, 226, 0.1), inset 0 0 0 0.1px rgba(0, 0, 0, 0.1);
}
.card-primary {
  background-color: $primary;
  border-color: $primary;
}
.card-success {
  background-color: $success;
  border-color: $success;
}
.card-info {
  background-color: $info;
  border-color: $info;
}
.card-warning {
  background-color: $warning;
  border-color: $warning;
}
.card-danger {
  background-color: $danger;
  border-color: $danger;
}

.card-partner-title {
  height: 46px;
  line-height: 46px;
  border-left: 3px solid $primary;
  padding-left: 20px;
  font-size: 12.5px;
  box-shadow: 0 2px 5px 0 rgba(74, 144, 226, 0.1), inset 0 0 0 0.1px rgba(0, 0, 0, 0.1);
}

.card-partner-item {
  height: 65px;
  padding-left: 14px;
  padding-left: 17px;
  font-size: 12.5px;
  box-shadow: 0 2px 5px 0 rgba(74, 144, 226, 0.1), inset 0 0 0 0.1px rgba(0, 0, 0, 0.1);
}

