.mini-stat {
  border-right: 1px solid #f1f1f1;
  margin-top: 5px;
  margin-bottom: 5px;
  //border-left: 2px solid $primary;
  /*border: 1px solid rgba($muted,0.12);*/
  padding: 10px 20px 10px 20px;
  //-webkit-border-radius: 5px;
  //-moz-border-radius: 5px;
  //border-radius: 5px;
  //margin-bottom: 20px;
  //box-shadow: 0 2px 5px 0 rgba(74, 144, 226, 0.1), inset 0 0 0 0.1px rgba(0, 0, 0, 0.1);
}

.mini-stat-icon {
  width: 60px;
  height: 60px;
  display: inline-block;
  line-height: 60px;
  text-align: center;
  font-size: 30px;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  float: left;
  margin-right: 10px;
  color: $white;
  background-color: rgba($white,0.1);
}

.mini-stat-info {
  font-size: 14px;
  /*margin: 20px 20px 20px 19px;*/
}

.mini-stat-info span {
  display: block;
  font-size: 24px;
  font-weight: 600;
  color: $primary;
}

.mini-stat-info span.counter {
  display: block;
  font-size: 24px;
  font-weight: 600;
  color: $primary;
}

.mini-stat-info span.text {
  display: block;
  font-size: 12.5px;
  font-weight: normal;
  color: black;
}
