/* ==============
  Form-elements
===================*/

.form-control-feedback {
  color:red;
  font-size: 10px;
  font-weight: normal;
  padding-left:10px;
  padding-top:5px;
}

select.form-control {
  border: solid 0.2px #d0d0d0;
  border-bottom : 1.5px solid $primary;
}

.form-control {
  border-radius: 2px;
  box-shadow: 2px -2px 3px 0 rgba(74, 144, 226, 0.09);
  background-color: #ffffff;
  border: solid 0.2px #d0d0d0;
  border-left : 1.5px solid $primary;
  font-size: 11px;
  font-weight: 300;
}

.form-control-datatable {
  background-color: #ffffff;
  font-size: 11px;
  font-weight: 500;
}

.form-control.has-danger {
  border-left : 1.5px solid red;
}

.form-control {
  &:focus {
    border-color: $primary;
    box-shadow: none;
  }
}

.custom-checkbox {
  .custom-control-input:checked~.custom-control-label::before {
  background-color: $primary;
  }
  .custom-control-input:focus~.custom-control-label::before {
    -webkit-box-shadow: 0 0 0 1px $white, 0 0 0 3px $primary;
    box-shadow: 0 0 0 1px $white, 0 0 0 3px $primary;
  }
}


.has-primary {
  .form-control {
    border-color: $primary;
    box-shadow: none;
  }
  .col-form-label,
  .form-control-feedback{
    color: $primary;
  }
}

.has-danger {
  .form-control {
    border-color: $danger;
    box-shadow: none;
  }
  .col-form-label,
  .form-control-feedback{
    color: $danger;
  }
}

.input-group-addon {
  border-radius: 2px;
  border: 1px solid $light;
}

.input-group-text {
  background-color: #39cfb4;
  color: white;
  box-shadow: 2px -2px 3px 0 rgba(74, 144, 226, 0.06);
  border: none;
}