}

/* button Outline */
.btn-outline-primary {
  color: $primary;
  border-color: $primary;
  border-top: none;
  border-bottom: 2px solid $primary;
  border-left: none;
  border-right: none;
  border-radius: 0px;

}
.btn-outline-success {
  color: $success;
  border-color: $success;
}
.btn-outline-info {
  color: $info;
  border-color: $info;
}
.btn-outline-warning {
  color: $warning;
  border-color: $warning;
}
.btn-outline-danger {
  color: $danger;
  border-color: $danger;
}
.btn-outline-dark {
  color: $dark;
  background-image: none;
  background-color: transparent;
  border-color: $dark;
}